@font-face {
    font-family: logo;
    src: url(../assets/fonts/logo.ttf);
}

@font-face {
    font-family: clue;
    src: url(../assets/fonts/clue.otf);
}

@font-face {
    font-family: board;
    src: url(../assets/fonts/board.otf);
}

.logo-row {
    display: flex;
    align-items: center;
}

.logo-text {
    font-family: logo, serif;
    font-size: 28vh;
    text-shadow: 12px 12px #000;

    margin-bottom: 0;
}

.clue-text {
    font-family: clue, serif;
    font-size: 3vh;
    text-shadow: 3px 3px #000;
}

.join-text {
    display: flex;
    flex-direction: row;

    padding-left: 28vw;
    padding-right: 28vw;
}

.join-text:before, .join-text:after{
    content: "";
    flex: 1 1;
    border-bottom: 1px solid;
    margin: auto;
}

.join-text:before {
    margin-right: 10px;
}

.join-text:after {
    margin-left: 10px;
}

.info-row {
    margin-top: 20px;
}

.info-heading {
    font-family: board, serif;
    font-size: 6vh;
    text-shadow: 5px 5px #000;
    letter-spacing: 0.02em;
}

.session-name-heading {
    margin-bottom: 0;
}

.session-name-text {
    font-family: board, serif;
    font-size: 15vh;
    color: #d69f4c;
    text-shadow: 8px 8px #000;
}

.start-game-button {
    padding-top: 10vh;
}

ul {
    padding-inline-start: 0 !important;
    list-style-type: none;
}

.leaderboard-player-names {
    text-align: right;
}

.leaderboard-scores {
    text-align: left;
}
